@import url(https://fonts.googleapis.com/css?family=Raleway);
* {
  font-family: "Raleway", sans-serif;
  box-sizing: border-box;
}

.title {
  position: relative;
  background-color: rgba(0, 0, 0, 1);
  color: rgb(255, 255, 255);
  padding: 20px;
  font-size: 1.8em;
  border-radius: 5px;
}

.question {
  font-size: 1.2em;
  font-weight: bold;
  margin-bottom: 5px;
}

.questionBox {
  position: relative;
  border: 1px solid rgba(0, 0, 0, 0.2);
  margin-top: 5px;
  padding: 15px;
  box-sizing: border-box;
  background-color: rgb(250, 250, 250);
}

.answerBtn {
  position: relative;
  padding: 10px;
  background-color: rgb(250, 151, 37);
  display: inline-block;
  margin: 10px;
  outline: none;
  border: none;
  font-size: 1em;
  color: rgb(255, 255, 255);
  font-weight: bold;
  border-radius: 4px;
  cursor: pointer;
}

.score-board {
  position: relative;
  height: 700px;
}

.score {
  position: absolute;
  width: 1024px;
  font-size: 3em;
  top: 250px;
  text-align: center;
}

.playbtn {
  position: absolute;
  font-size: 1.4em;
  padding: 10px;
  top: 350px;
  background-color: rgb(46, 182, 46);
  color: rgb(255, 255, 255);
  width: 150px;
  left: 0px;
  right: 0px;
  margin: auto;
  outline: none;
  cursor: pointer;
}

.spinner-container {
  z-index: 0;
  position: absolute;

}

.game-title {
  z-index: 1;
  position: relative;
  font-size: 32px;
  color: skyblue;
  font-weight: bolder;
  text-shadow: 2px 2px darkred;

}

.setting-btn {
  margin-top: 1%;
    margin-bottom: 5%;
}

.play-btn {
  display: flex;
  margin-top: 5%;
  padding-right: 40%;
  padding-left: 40%;
  height: 8vh;
}

.game-description {
  margin-top: 10%;
}

.settingMargin {
  margin-top: 5%;
    padding-left: 35%;
    padding-right: 35%;
}

.formMarginbottom {
  margin-bottom: 3%;
}

.settingLabel {
  font-size: 20px;
}

.fade-enter {
  opacity: 0;
  z-index: 1;
}
.fade-enter.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 250ms ease-in;
  transition: opacity 250ms ease-in;
}

.labelTopMargin {
    margin-top: 10%;
}

.labelBottomMargin {
    margin-bottom: 10%;
}

.feedback-text {
  background-color: #ccccff;
  line-height: 2;
  box-shadow: 10px 0 0px 0px #EDC330, -10px 0 0px 0px #EDC330;
  border-bottom-left-radius: 100px;
  border-bottom-right-radius: 100px;
}

.positive-shadow {
  box-shadow: 10px 0 0px 0px green, -10px 0 0px 0px green;
}

.negative-shadow {
  box-shadow: 10px 0 0px 0px red, -10px 0 0px 0px red;
}

.space-top {
  margin-top: 5%;
}

.text-center {
  text-align: center;
}

.loading-design {
  font-size: 36px;
  font-weight: bolder;
}
.quizContainer {
  /* position: absolute; */
  width: 100%;
  height: 100%;
  border: 1px solid rgba(0, 0, 0, 0.2);
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 10px;
  box-sizing: border-box;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  margin: auto;
}


.quizContainer .tableTitle {
    position: relative;
    background-color: rgba(0, 0, 0, 1);
    color: rgb(0, 0, 0);
    padding: 20px;
    font-size: 1.8em;
    border-radius: 5px;

}

.tableTitle .table {
    width: 100%;
    table-layout: fixed;
}

.tableTitle .table .textLeft {
    text-align: left;
}

.tableTitle .table .textCenter {
    text-align: center;
}
.tableTitle .table .textRight {
    text-align: right;
}

.timer {
    height: 100%;
    color: white;
}

.score-board {
  position: relative;
  height: 600px;
  width: 100%;
  align-items: center;
}

.score {
    margin-top: -5%;
  position: relative;
  width: 100%;
  font-size: 3em;
  /* top: 250px; */
  text-align: center;
}

.playBtn {
  margin-top: 5%;
}
.homeBtn {
  margin-top: 1%;
  position: absolute;
  font-size: 1.4em;
  padding: 10px;
  top: 350px;
  background-color: rgb(46, 182, 46);
  color: rgb(255, 255, 255);
  width: 150px;
  left: 100px;
  right: 0px;
  margin: auto;
  outline: none;
  cursor: pointer;
}

.test {
  border: 10px solid red;
}

.question-container {

  margin-right: 10%;
  margin-left: 10%;
}

.white-background {
    background-color: white;
    height: 70vh;
}

.question {
  padding-top: 5%;
}

.question-text {
    font-size: 18px;
}

.answer-button {
  min-width: 50%;
}

.make-it-smaller {
    display: flex;
    margin-top: 5%;
    padding-right: 10%;
    padding-left: 10%;
    height: 8vh;
}

.text-left {
    text-align: left;
}

.green-text {
    color: green;
    font-weight: bolder;
    background-color: green;
}

.red-text {
    color: darkred;
}

.game-over-text {
  font-size: 2.8em;
}

.score-panel {
  background-color: #00ff0081;
  margin-top: 10%;
  margin-bottom: 10%;
  padding: 2%;
  border-radius: 10px;
}

.score-panel-container {
  padding-right: 30%;
  padding-left: 30%;
}
.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        -webkit-animation: App-logo-spin infinite 20s linear;
                animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    min-height: 30vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    /*background-image: linear-gradient(to right, skyblue, white, lightgreen);*/
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

